.slick-slider {
    position: relative;
    width: auto;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: fadeIn 1s ease-in-out;
    margin-top: 70px;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.slick-track {
    display: flex;
}


.slick-slide img {
    width: 100%;
    background-size: cover;
    max-width: 100%;
    height: 80vh;
    max-height: 100%;
    margin: auto;
}


ul .slick-dots {
    position: absolute;
    bottom: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
}

.slick-dots li button:before {
    font-size: 12px;
    color: #ddd;
}

.slick-dots li.slick-active button:before {
    color: #333;
}
@media screen and (max-width: 800px) {
    .slick-slide img {
        height: auto;
        
    }
    .slick-slider {
        margin-top: 30px;
    
    }
}

@media screen and (max-width: 450px) {
    .slick-slide img {
        height: 40vh;
    }
    
}